@import "../../common//styles/sass/variables.scss";
@import "../../common/styles/sass/mixins.scss";
.project {
	justify-content: center;
	align-items: center;
	background-color: $bgColor;
	padding: 5px;
	flex-basis: calc(33.33% - 20px);
	flex-grow: 1;
	margin: 10px;
	@media (max-width: 1142px) {
		flex-basis: calc(50% - 20px);

	}
	@media (max-width: 600px) {
		flex-basis: calc(100% - 20px);
		margin: 10px 0;
	}
	.projectImg {
		height: 200px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		position: relative;
		&::before {
			position: absolute;
			content: "";
			background-color: $bgColor;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			opacity: 0.3;
		}
	}

	.projectTitle {
		letter-spacing: 1px;
		margin: 0 0 5px;
		@include text($primaryFont, 16px, 1.2, 400);
	}
	.description {
		color: $textColor;
		font-size: 16px;
		line-height: 1.8;
		font-family: $secondaryFont;
	}
	.projectInfo {
		padding: 20px;
	}
}
